import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		// 使用vuex保存当前用户身份 1 为普通用户   2 为 管理员
		uid: 2,
		// 保存练习题数据
		practiceObj:{
			// 自己发送的练习题
			selfSendPracticeList:[]
		},
		// 保存当前请求地址
		url: 'http://120.24.6.91:8082'
	},
	mutations: {
		setUid (state, val) {
			state.uid = val;
		}
	}
}) 




