<template>
	<div class="register">
		<div class="register_page" v-show="toggle">
			<div class="register_div">
				<i class="iconfont icon-nicheng"></i>
				<input type="text" placeholder="请输入昵称" v-model="nickname" />
			</div>
			<div class="register_div">
				<i class="iconfont icon-shouji"></i>
				<input type="text" placeholder="请输入手机号" v-model="phone" />
			</div>
			<div class="register_div">
				<i class="iconfont icon-mima"></i>
				<input type="password" placeholder="请输入密码" v-model="password" />
			</div>
			<div class="verification ">

				<input type="text" placeholder="请输入验证码" v-model="verification_code" />
				<img src="http://120.24.6.91:8082/tool/code" class="verification_img" />
			</div>
			<p class="nextone" @click="clicknext">下一步</p>
		</div>
		<div class="phone_code_page register_page" v-show="toggle2">
			<div class="verification ">
				<input type="text" placeholder="请输入验证码" v-model="phone" />
				<p>重新获取<i>(60)</i></p>
			</div>
			<div class="register_div">

				<input type="text" placeholder="请输入手机验证码" />
			</div>
			<p class="nextone">注册</p>

		</div>
	</div>
</template>

<script>
	import Vue from "vue";
	import { Toast } from 'mint-ui';
	export default {
		name: 'register',
		data() {
			return {
				nickname: "",
				phone: "",
				password: "",
				verification_code: "",
				phone_num: "",
				verification_result: '',
				guid: "",
				toggle: true,
				toggle2: false
			}
		},
		methods: {
			clicknext() {

				if(this.nickname == "" || this.phone == "" || this.password == "" || this.verification_code == "") {
					Toast({
						message: '请输入内容',
						position: 'middle',
						duration: 1000
					});
				}
				if(this.nickname !== "" && this.phone !== "" && this.password !== "" && this.verification_code !== "") {
					this.$http.get("http://120.24.6.91:8082/tool/sendmsgbind?viriCode=" + this.verification_code+"&tel="+this.phone).then((response) => {
						console.log(response.body)
						this.verification_result = response.body;
						
					})
				}

			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import '../../assets/css/var.scss';
	.register {
		position: relative;
		top: 130px;
		height: calc(100vh - 130px);
		:-ms-input-placeholder {
			color: $pagebg;
			font-size: $font12;
		}
		div.register_div {
			height: 52px;
			line-height: 52px;
			width: 70%;
			margin: auto;
			background-color: $whitecolor;
			border: 1px solid $barbg;
			margin-bottom: 15px;
			border-radius: 2px;
			input {
				border: none;
				padding: 0;
				margin: 0;
				vertical-align: top;
				height: 50px;
				line-height: 50px;
				width: 70%;
				font-size: $font16;
				padding: 10px;
				box-sizing: border-box;
				/*	 background: #00f;*/
			}
			i {
				font-size: 25px;
				padding: 0 8px;
			}
		}
		div.verification {
			display: flex;
			flex-direction: row;
			height: 52px;
			line-height: 52px;
			width: 70%;
			margin-left: 15%;
			margin-bottom: 15px;
			position: relative;
			input {
				border-radius: 2px;
				background-color: $whitecolor;
				border: 1px solid $barbg;
				padding: 10px;
				box-sizing: border-box;
				margin-right: 10px;
				width: calc(100% - 100px);
			}
			.verification_img {
				width: 90px;
				height: 50px;
			}
		}
		.nextone {
			height: 52px;
			line-height: 52px;
			text-align: center;
			color: $bluecolor;
			background-color: $whitecolor;
			width: 62%;
			margin: auto;
			border-radius: 2px;
			font-size: $font16;
		}
		.phone_code_page {
			.verification p {
				display: inline-block;
				background-color: $whitecolor;
				text-align: center;
				color: $bluecolor;
				width: 90px;
			}
			i {
				font-style: normal;
			}
		}
	}
</style>


