<script>

// 工具函数
import util from '@/assets/js/util'
// 消息提示框组件
import { Toast } from 'mint-ui';
// 表情
import expression from '../functionComponents/expression'
// 选择练习题题目组件
import selectPracticeTitle from '../functionComponents/selectPracticeTitle'
// 签到组件
import signIn from '../functionComponents/signIn'
// 进入每日一题结果组件
import enterExaminationResult from '../functionComponents/enterExaminationResult'
// 上拉加载组件
import { Loadmore } from 'mint-ui'
// 消息时间组件
import msgTime from '../functionComponents/msgTime'
// 消息撤回组件
import msgWithdraw from '../functionComponents/msgWithdraw'
// 更多功能组件
import moreFunction  from '../functionComponents/moreFunction'

export default {
  name: 'home',
  data () {
    return {
      // 发送按钮
      isSend: false,
      // 展开更多功能
      isMoreFunction: false,
      // 签到
      isSignIn: false,
      // 表情
      isExpression: false,
      // 发送的文本消息
      chatInput: '',
      // 待选择的练习题
      practiceSelect: false,
      // 自己发送的数据
      selfArr: [],
      // 别人发送的数据
      otherArr: [],
      // 发送的练习题题目
      practiceArr: [
        {
          options: 'A',
          text: '损失',
          on: false,
          off: false
        },
        {
          options: 'B',
          text: '费用',
          on: false,
          off: false
        },
        {
          options: 'C',
          text: '负债',
          on: false,
          off: false
        },
        {
          options: 'D',
          text: '所有者权益',
          on: false,
          off: false
        }
      ]
    }
  },
  // 进入该路由时调用
  beforeRouteEnter (to, from, next) {
    next(vm => {
      // 滚动到底部
      util.partScrollToBottom();
    });
  },
  mounted () {

    this.$http({
      url: 'http://120.24.6.91:8082/tool/sendmsgbind',
      method: 'POST',
      params: {
         validateCode: 1234,
				 tel: 11111
      }
  }).then(function(response) {

      console.log(response);

  }, function(response) {
    console.log(response);
  });




    // 获取vuex状态
    // console.log(this.$store.state.id);
    // 更改vuex状态
    // this.$store.commit('increment',20)

    // 初始化
    this._init();

    let doc = document.body || document.documentElement;
    // 全局回车事件
    doc.onkeydown = (event) => {
      if (event.keyCode === 13) {
        if (this.chatInput) {
          // 发送
          this._send();
        }
      }
    };



  },
  methods: {
  	// 上拉加载
  	loadTop() {
  		// 关闭加载动画
  		setTimeout(() => {
  			this.$refs.loadmore.onTopLoaded();
  		},3000)
  	},
    // 获取焦点
    getInputFocus() {
      this.isSend = true;
      // 隐藏更多功能和表情
      this.isMoreFunction = false;
      this.isExpression = false;
    },
    // 练习题选项单选
    practiceOptionsClick(event, index) {
      // 当前元素所有的同级元素删除 'on' 样式
      let li = event.target.parentNode.children;
      for(let i in li) {
         util.removeClass(li[i],'on');
      }
      // 当前选中的元素添加 'on' 样式
      util.addClass(event.target,'on')
    },
    // 练习题选项提交
    practiceOptionsSubmit(event,index) {
      let list = this.$store.state.practiceObj.selfSendPracticeList[index];
      console.log(list);
      let textRadio = event.target.parentNode.children;
      let li = '';
      let optionsIndex = false;
      for (let i in textRadio) {
        if(util.hasClass(textRadio[i],'text-radio')) {
          li = textRadio[i].children[0].children;
        }
      }
      for (let i in li) {
        if (util.hasClass(li[i],'on')) {
          optionsIndex = i;
        }
      }
      if (!optionsIndex) {
        Toast('请选择你的答案');
        return false;
      }
     
      // 判断正确答案
      if (list.options[optionsIndex].IsTrue == 1) {
        // 保存用户答题记录
        this._ajax(`${this.$store.state.url}/wx/powderSystem/addDayPractiseQuestionLog?qid=${list.Id}&optionId=${list.options[optionsIndex].Id}&isTrue=0&uid=${userObj.uid}&openId=${userObj.openId}&addDayPractiseQuestionLog=${list.PractiseItem}`, (res) => {
          // 保存参与人数
          this.$set(list, 'participate', res.body);
        });
      } else {
        // 保存用户答题记录
        this._ajax(`${this.$store.state.url}/wx/powderSystem/addDayPractiseQuestionLog?qid=${list.Id}&optionId=${list.options[optionsIndex].Id}&isTrue=1&uid=${userObj.uid}&openId=${userObj.openId}&addDayPractiseQuestionLog=${list.PractiseItem}`, (res) => {
           // 保存参与人数
          this.$set(list, 'participate', res.body);
        });
        util.addClass(li[optionsIndex],'off');
      }
       // 显示正确答案

    
      this.$set(list, 'submitStatus', true);
    },
    // 打开表情
    openExpression() {
      this.isMoreFunction = false;
      this.isExpression = !this.isExpression;
      // 滚动到底部
      this.$nextTick(() => {
        util.partScrollToBottom();
      });
    },
    // 打开更多功能
    openMoreFunction() {
      this.isExpression = false;
      this.isMoreFunction = !this.isMoreFunction;
      // 滚动到底部
      this.$nextTick(() => {
        util.partScrollToBottom();
      });
    },
    // 签到
    receliveSignIn(val) {
     this.isSignIn = val;
     this.$nextTick(() => {
       util.partScrollToBottom();
     });
    },
    // 按钮发送
    chatInputSend() {
      if (this.chatInput) {
        this._send();
      }
    },
    // 初始化
    _init(){
      // 滚动到底部
      util.partScrollToBottom();
      setInterval(() => {
        let val = document.getElementById('msg').value;
        // console.log(val);
        try {
          let s = JSON.parse(val);
          if(s.userid == userObj.uid){
            s.ismy=true;
          }else{
            s.ismy=false;
          }
          
          if (s.userid) {
              // 判断是否接收到的是练习题
              // dataType：1 为文本消息  2  为练习题   
              let list = this.$store.state.practiceObj.selfSendPracticeList;
              if (s.dataType == 1) {
                console.log('初始化');
                console.log(s);
                if (list.length <= 0) {
                  list.push(s);
                  // 每次获取到新消息就自动滚动到底部
                  this.$nextTick(() => {
                    util.partScrollToBottom();
                  });
                } else {
                    if (list[list.length-1].contentId != s.contentId && !s.ismy) {
                        list.push(s);
                        // 每次获取到新消息就自动滚动到底部
                        this.$nextTick(() => {
                          util.partScrollToBottom();
                        });
                    }
                }
              } else if(s.dataType == 2) {
                  if (list.length <= 0) {
                    list.push(s);
                  } else {
                    if (list[list.length-1].Id != s.Id && !s.ismy) {
                      list.push(s);
                      // 每次获取到新消息就自动滚动到底部
                      this.$nextTick(() => {
                        util.partScrollToBottom();
                      });
                    }
                  }
              }
              
            
          }
          // console.log(this.otherArr);
        } catch(err) {
          // console.log('错误了');
          // console.log(err);
        }
     },200);
    },
    // 提交
    _submit(type, content, contentId) {
      let time = new Date();
      let obj = {
          userid: userObj.uid,
          username: "刘小龙",
          useravator: "./face.png",
          type: type,
          content: content,
          contentId: contentId,
          timer: time.getTime(),
          ismy: true,
          dataType: 1
        };
        console.log('提交');
        console.log(obj);
        // 发送websocket
        MessageSend(JSON.stringify(obj));
        this.$store.state.practiceObj.selfSendPracticeList.push(obj);
        // 等待DOM已经被插入进入的时候，再滚动到底部
        this.$nextTick(() => {
          util.partScrollToBottom();
        });
    },
    // ajax 请求
    _ajax(url, cb) {
      this.$http.get(url).then( res => {
        if (res.status === 200){
            cb && cb(res);
        }
      }, error => {
       console.log(error);
     });
    },
    // 发送
    _send() {
      // 判断消息类型是否是字符串
      let msgType = typeof this.chatInput == 'string' ? 1 : 0;
      
      // 获取消息id
      let msgId = null;
      let time = new Date().getTime();
      this._ajax(`${this.$store.state.url}/wx/powderSystem/addChatLog?uid=${userObj.uid}&openId=${userObj.openId}&msgType=${msgType}&msgContent=${this.chatInput}&groupId=${userObj.groupId}&timer=${time}`, (res) => {
        console.log(res);
        if (res.status === 200) {
            msgId = res.body;
            this._submit(msgType, this.chatInput, msgId);
            this.chatInput = '';
            this.isSend = false;
        }
      });
    }
  },
  // 注册组件
  components: {
  	mtLoadmore: Loadmore,
    msgTime,
    msgWithdraw,
    moreFunction,
    enterExaminationResult,
    signIn,
    selectPracticeTitle,
    expression
  }
}
</script>

