    }
    .participate{
      position: absolute;
      bottom: -30px;
      right: 20px;
      color: $iconcolor;
      .iconfont{
        font-size: $font18;
      }
      span {
        color: $bluecolor;
      }
    }
  }
}

/* 自己-练习消息 */
.self-practice{
  margin-bottom: 30px;
  .chat-msg{
    position: relative;
    padding-right: 10px;
    text-align: right;
   .text{
      padding: 0;
      text-align: left;
      &:after{
        left: auto;
        right: -5px;
      }
      &:before{
        left: auto;
        right: -1px;
      }
      & > span {
        display: inline-block;
        padding: 10px 10px 0 10px;
      }
      .text-radio{
        padding: 0 10px;
        li{
          margin-top: 10px;
          font-size: $font16;
          color: #666;
          &.on{
            color: $bluecolor;
            & > span {
              border: 1px solid $bluecolor;
            }
          }
          &.off{
            color: $errorcolor;
            & > span {
              border: 1px solid $errorcolor;
            }
          }
          & > span{
            display: inline-block;
            margin-right: 10px;
            width: 25px;
            height: 25px;
            line-height: 25px;
            text-align: center;
            border-radius: 50%;
            border: 1px solid #666;
          }
        }
      }
      .text-btn{
        margin-top: 15px;
        padding: 12px 0;
        @include border-top-1px($bluecolor);
        text-align: center;
        color: $bluecolor;
        font-size: $font16;
      }
      .text-answer{
        margin-top: 10px;
        padding: 10px;
        background: #f1faff;
        color: $bluecolor;
        font-size: $font18;
      }
      .text-success{
        .text-detail{
          margin-top: 8px;
          padding: 0 10px;
          line-height: 28px;
          color: $iconcolor;
          font-size: $font16;
          b{
            margin-right: 7px;
            color: $txtcolor;
            font-weight: normal;
          }
        }
        .text-flex{
          margin-top: 10px;
          display: flex;
          .text-flex-span{
            display: block;
            flex: 1;
            min-width: 0;
            text-align: center;
            p{
              font-size: $font10;
              color: $iconcolor;
            }
            h3{
             font-size: $font14;
            }
          }
          .text-flex-border{
            display: block;
            flex: 1;
            min-width: 0;
            border-right: 1px solid $bordercolor;
            text-align: center;
            &:last-child{
              border: none;
            }
          }
        }
      }
    }
    .participate{
      position: absolute;
      bottom: -30px;
      right: 0;
      color: $iconcolor;
      .iconfont{
        font-size: $font18;
      }
      span {
        color: $bluecolor;
      }
    }
  }
}



</style>



